import React, { useEffect, useRef, useState } from "react";
import style from "./Shelf.module.scss";
import { motion } from "framer-motion";
import BookPoster from "../BookPoster/BookPoster";
import { useSelector } from "react-redux";
export default function Shelf({ shelfType }) {
  const { allBooks } = useSelector(state => state.bookReducer);
  const InnerCarousel = useRef();
  const [carouWidth, setcarouWidth] = useState(0);
  function titleShelf(shelfType) {
    switch (shelfType) {
      case "currentlyReading":
        return "Current Reading";

      case "wantToRead":
        return "Want Reading";

      case "read":
        return "Read";
      default:
        break;
    }
  }

  useEffect(() => {
    setcarouWidth(
      InnerCarousel.current.scrollWidth - InnerCarousel.current.offsetWidth
    );
  }, [allBooks]);
  return (
    <div className="current  d-flex col-12">
      <div className="col-3">
        <h2 className={`${style.headTreanding} position-relative col-10`}>
          {titleShelf(shelfType)}
        </h2>
      </div>
      <motion.div
        whileTap={{ cursor: "grabbing" }}
        className={`${style.carousel} col-9 `}
      >
        <motion.div
          ref={InnerCarousel}
          drag="x"
          dragConstraints={{ right: 0, left: -carouWidth }}
          className={`${style.InnerCarousel} d-flex row noWrap mt-2`}
        >
          {allBooks.map((book, index) => {
            if (book.shelf === shelfType) {
              return (
                <BookPoster
                  book={book}
                  key={index}
                  allBooks={allBooks}
                />
              );
            }
            return true;
          })}
        </motion.div>
      </motion.div>
    </div>
  );
}
