import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import Shelf from "../Shelf/Shelf";

export default function AllBooks() {
  const { allBooks } = useSelector(state => state.bookReducer);

  useEffect(() => {
  }, [allBooks]);

  return (
    <div className="d-flex flex-column col-12  position-relative minHvh75">
      {allBooks ? (
        <>
          <Shelf shelfType={"currentlyReading"} />
          <Shelf shelfType={"wantToRead"} />
          <Shelf shelfType={"read"} />
        </>
      ) : (
        <i class="fa-solid fa-spinner fa-2x fa-spin  position-absolute top-50 start-50 translate-middle "></i>
      )}
    </div>
  );
}
