import React, { useEffect, useState } from "react";
import style from "./Home.module.scss";
import { search } from "../BookApi/BookApi";
import AllBooks from "../AllBooks/AllBooks";
import Search from "../Search/Search";
import { Link, Route, Routes, useNavigate } from "react-router-dom";
import { debounce } from "throttle-debounce";
import Book from "../Book/Book";
import Current from "../BookType/Current";
import Want from "../BookType/Want";
import Read from "../BookType/Read";
import { useDispatch, useSelector } from "react-redux";
import { getAllBooks } from "../../Redux/bookSlice";
export default function Home() {
  const { allBooks } = useSelector(state => state.bookReducer);
  const dispatch = useDispatch();
  const [searchBooks, setsearchBooks] = useState([]);
  const navigate = useNavigate();
  const [SearchIcon, setSearchIcon] = useState(true);
  async function searchDataFetch(query, maxResult) {
    if (!query.trim()) {
      setsearchBooks([]);
    } else {
      const debounceFunc = debounce(250, async (query, maxResult) => {
        let x = await search(query, maxResult);
        setsearchBooks(x);
      });
      debounceFunc(query, maxResult);

    }
  }
  useEffect(() => {
    dispatch(getAllBooks());
  }, []);

  return (
    <div className={`${style.Home} col-10 p-4 vh-100`}>
      <div>
        <div className="col-4">
          <h3>your books</h3>
        </div>
        <div className="col-4 d-flex flex-row justify-content-end  ">
          <div to="search" className={style.searchBox}>
            <input
              className="search-input"
              type="text"
              name=""
              placeholder="book name"
              onKeyUp={(e) => {
                searchDataFetch(e.target.value, 10);
                navigate("/search");
                setSearchIcon(false);
              }}
            />
            <span className="search-close-btn">
              {SearchIcon ? (
                <Link
                  to="/search"
                  className={`${style.faSearch} fas fa-search`}
                ></Link>
              ) : (
                <Link
                  to="/"
                  className={`${style.faXmark} fa-solid fa-xmark`}
                  onClick={() => {
                    setSearchIcon(true);
                  }}
                ></Link>
              )}
            </span>
          </div>
          <div className={style.addIcon}>
            <i className={`${style.faPlus} fa-solid fa-plus`}></i>
          </div>
        </div>
      </div>
      <div className={`py-4`}>
        <Routes>
          <Route
            path="/"
            element={
              <AllBooks

              />
            }
          ></Route>
          <Route
            path="/search"
            element={
              <Search
                searchBooks={searchBooks}
              />
            }
          ></Route>
          <Route path="/Details" element={<Book />}></Route>
          <Route
            path="/current"
            element={
              <Current />
            }
          ></Route>
          <Route
            path="/want"
            element={
              <Want />
            }
          ></Route>
          <Route
            path="/read"
            element={
              <Read />
            }
          ></Route>
        </Routes>
      </div>
    </div>
  );
}
