import React from "react";
import { useSelector } from "react-redux";
import BookPoster from "../BookPoster/BookPoster";
export default function Want() {
  const {allBooks}=useSelector(state=>state.bookReducer);

  return (
    <div className="d-flex col-12">
      <div className="col-3">
        <h2 className={`position-relative col-10`}>
          Want Reading
        </h2>
      </div>
      <div className={`col-9 d-flex row  mt-2`}>
        {allBooks.map((book, index) => {
          if (book.shelf === "wantToRead") {
            return (
              <BookPoster
                book={book}
                key={index}
                allBooks={allBooks}

              />
            );
          }
        })}
      </div>
    </div>
  );
}
