import React, { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { get } from "../BookApi/BookApi";
import style from "./Book.module.scss";
export default function Book() {
  const [SerachParam, setSerachParam] = useSearchParams();
  const id = SerachParam.get("id");
  const [Book, setBook] = useState({});
  async function BookDetails(id) {
    let x = await get(id);
    setBook(x);
  }
  useEffect(() => {
    BookDetails(id);
  }, []);

  return (
    <>
      {Object.keys(Book).length > 0 ? (
        <div className="col-12 p-4 row gap-2">
          <div className="col-md-3 col-sm-10">
            {Book.imageLinks ? (
              <img src={Book.imageLinks.thumbnail} className="w-100" alt=" " />
            ) : (
              <img alt="" />
            )}
            <h6 className={style.pp}>{Book.authors}</h6>
            <div className="col my-3">
              {Book.categories &&
                Book.categories.map((category, index) => {
                  return (
                    <div className="btn btn-primary btn-sm m-2" key={index}>
                      {category}
                    </div>
                  );
                })}
            </div>
          </div>
          <div className="col-md-7 col-sm-10 ">
            <h1 className="mb-2">{Book.title}</h1>

            <p className={style.pp + " py-1"}>{Book.description}</p>
          </div>
        </div>
      ) : (
        <p>is loading</p>
      )}
    </>
  );
}
