import React from "react";
import { Link, useNavigate } from "react-router-dom";
import style from "./SideNavbar.module.scss";
export default function SideNavbar() {
  return (
    <>
      <div className={`${style.sideNav} col-2 vh-100 d-flex flex-column `}>
        <Link to="/" className={`${style.logo}`}>
          <i className="fa-solid fa-book-bookmark">Books</i>
        </Link>
        <div className={`${style.category}`}>
          <ul>
            <li>
              <Link to="/" className="fa-solid fa-align-justify">
                <span>All</span>
              </Link>
            </li>
            <li>
              <Link to="/current" className="fa-solid fa-book-open-reader">
                <span>Currently</span>
              </Link>
            </li>
            <li>
              <Link to="/want" className="fa-solid fa-eye">
                <span>Want</span>
              </Link>
            </li>
            <li>
              <Link to="/read" className="fa-solid fa-book">
                <span>Read</span>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
}
