import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { getAll, update } from "../Components/BookApi/BookApi";
const initialState = {
    allBooks: [],
    loading: null,
    error: false
}

export const getAllBooks = createAsyncThunk("Books/get", async () => {
    return await getAll();
});
export const updateBookShelf = createAsyncThunk("Book/update", async (bookToUpdate) => {
    console.log(bookToUpdate.book, bookToUpdate.type);
    update(bookToUpdate.book, bookToUpdate.type);
    return bookToUpdate;
})
export const bookSlice = createSlice({
    name: "books",
    initialState,
    reducers: {
   
    },
    extraReducers: {
        [getAllBooks.pending]: (state, action) => {
            state.loading = true;
        },
        [getAllBooks.fulfilled]: (state, action) => {
            state.loading = false;
            state.allBooks = action.payload;
        },
        [getAllBooks.rejected]: (state, action) => {
            state.loading = false;
            state.error = true;
        },
        [updateBookShelf.pending]: (state, action) => {
            state.loading = true;
        },
        [updateBookShelf.fulfilled]: (state, action) => {
            state.loading = false;
            let findBook = false;
            let updatedBooks = state.allBooks.filter((book) => {
                if (book.id === action.payload.book.id) {
                    book.shelf = action.payload.type;
                    findBook = true;
                }
                return book.shelf !== "none";
            });
            if (!findBook) {
                updatedBooks = [...state.allBooks, { ...action.payload.book, shelf: `${action.payload.type}` }];
            }
            state.allBooks = updatedBooks;
        },
        [updateBookShelf.rejected]: (state, action) => {
            state.loading = false;
            state.error = true;
        }

    }
})

export const { } = bookSlice.actions;

export default bookSlice.reducer;