import React from "react";
import { useSelector } from "react-redux";
import BookPoster from "../BookPoster/BookPoster";
export default function Search({ searchBooks }) {
  const { allBooks } = useSelector(state => state.bookReducer);

  return (
    <div className="col-12 row ">
      {searchBooks.error ? (
        <h2>not found</h2>
      ) : searchBooks.length > 0 ? (
        searchBooks.map((book, index) => {
          return (
            <BookPoster
              book={book}
              key={index}
              allBooks={allBooks}
            />
          );
        })
      ) : (
        <h3>write search</h3>
      )}
    </div>
  );
}
