import { motion } from "framer-motion";
import React, { useRef, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { updateBookShelf } from "../../Redux/bookSlice";
import style from "./BookPoster.module.scss";
export default function BookPoster({ book }) {
  const { allBooks } = useSelector(state => state.bookReducer);
  const dispatch = useDispatch();
  let none = useRef();
  let wantToRead = useRef();
  let read = useRef();
  let currentlyReading = useRef();
  const [Shelf, setShelf] = useState(" ");
  useEffect(() => {
    switch (Shelf) {
      case "currentlyReading":
        currentlyReading.current.className = "dropdown-item active";
        wantToRead.current.className = "dropdown-item ";
        read.current.className = "dropdown-item ";
        none.current.className = "dropdown-item ";
        break;
      case "wantToRead":
        currentlyReading.current.className = "dropdown-item ";
        wantToRead.current.className = "dropdown-item active";
        read.current.className = "dropdown-item ";
        none.current.className = "dropdown-item ";
        break;
      case "read":
        currentlyReading.current.className = "dropdown-item ";
        wantToRead.current.className = "dropdown-item ";
        read.current.className = "dropdown-item active";
        none.current.className = "dropdown-item ";
        break;
      case " ":
        currentlyReading.current.className = "dropdown-item ";
        wantToRead.current.className = "dropdown-item ";
        read.current.className = "dropdown-item ";
        none.current.className = "dropdown-item active";
        break;
    }
  }, [Shelf]);
  useEffect(() => {
    allBooks.map((myBook) => {
      if (myBook.id === book.id) {
        setShelf(myBook.shelf);
        return true;
      }
    });
  }, []);

  let navigate = useNavigate();
  function goToDetails(id) {
    navigate({
      pathname: "/Details",
      search: `?id=${id}`,
    });
  }

  return (
    <motion.div
      className={`${style.BookPoster} col-3 position-relative`}
      id="Book"
      onClick={() => {
        goToDetails(book.id);
      }}
    >
      {book ? (
        <>
          {book.imageLinks ? (
            <img src={book.imageLinks.smallThumbnail} alt="" />
          ) : (
            <img alt="" />
          )}
          <div
            className={style.evaluation}
            id="evaluation"
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            <i
              className="position-absolute w-100 top-50 start-50 translate-middle col-10 nav-link fa-solid fa-plus"
              id="navbarDropdown"
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            ></i>
            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
              <li>
                <span
                  className="dropdown-item"
                  ref={currentlyReading}
                  onClick={() => {
                    dispatch(updateBookShelf({ book, type: "currentlyReading" }));
                    setShelf("currentlyReading");
                  }}
                >
                  currently
                </span>
              </li>
              <li>
                <span
                  className="dropdown-item"
                  ref={wantToRead}
                  onClick={() => {
                    dispatch(updateBookShelf({ book, type: "wantToRead" }));
                    setShelf("wantToRead");
                  }}
                >
                  want
                </span>
              </li>
              <li>
                <span
                  className="dropdown-item"
                  ref={read}
                  onClick={() => {
                    dispatch(updateBookShelf({ book, type: "read" }));
                    setShelf("read");
                  }}
                >
                  Read
                </span>
              </li>
              <li>
                <span
                  className="dropdown-item"
                  ref={none}
                  onClick={() => {
                    dispatch(updateBookShelf({ book, type: " " }));
                    setShelf(" ");
                  }}
                >
                  none
                </span>
              </li>
            </ul>
          </div>
          <h6>{book.title}</h6>
          <p className={style.author}>{book.authors}</p>
        </>
      ) : (
        <i class="fa-solid fa-spinner fa-2x fa-spin  position-absolute top-50 start-50 translate-middle "></i>
      )}
    </motion.div>
  );
}
